<template lang="">
  <div id="quizgame">
    <div v-if="welcome" class="welcome">
      <!--<h1>Quiz Game</h1>-->
      <img src="../assets/img/quizgame/quizgame.png" alt="enter quiz game" />
      <div class="flags">
        <div class="flag" @click="showGamePlay('eng')">
          <img src="../assets/img/quizgame/flag-en.png" alt="english" />
        </div>
        <div class="flag" @click="showGamePlay('ita')">
          <img src="../assets/img/quizgame/flag-it.png" alt="italiano" />
        </div>
        <div class="flag" @click="showGamePlay('fra')">
          <img src="../assets/img/quizgame/flag-fr.png" alt="français" />
        </div>
      </div>
    </div>
    <div v-if="gameplay" class="gameplay">
      <!--<h2>Select</h2>-->
      <div class="cards">
        <div @click="play('movie')" class="card">
          <div class="card_1"></div>
          <span v-if="this.lang == 'eng'">Movies</span>
          <span v-if="this.lang == 'ita'">Film</span>
          <span v-if="this.lang == 'fra'">Films</span>
        </div>
        <div @click="play('music')" class="card">
          <div class="card_2"></div>
          <span v-if="this.lang == 'eng'">Music</span>
          <span v-if="this.lang == 'ita'">Musica</span>
          <span v-if="this.lang == 'fra'">Musique</span>
        </div>
        <div @click="play('sport')" class="card">
          <div class="card_3"></div>
          <span v-if="this.lang == 'eng'">Sports</span>
          <span v-if="this.lang == 'ita'">Sport</span>
          <span v-if="this.lang == 'fra'">Sports</span>
        </div>
        <div @click="play('general')" class="card">
          <div class="card_4"></div>
          <span v-if="this.lang == 'eng'">General K</span>
          <span v-if="this.lang == 'ita'">Cultura G</span>
          <span v-if="this.lang == 'fra'">Culture G</span>
        </div>
      </div>
    </div>

    <div class="games">
      <transition>
        <section class="show" v-if="game">
          <div class="control">
            <span @click="back"
              ><i class="fa-solid fa-circle-left fa-2x"></i
            ></span>
            <h2 class="title">{{ currentGame.title }}</h2>
            <span @click="infos"
              ><i class="fa-solid fa-circle-info fa-2x"></i
            ></span>
          </div>
          <div class="console">
            <div class="timer">
              <span class="clock"></span>
              <p class="mb-0">0:{{ secondsLeft }}</p>
            </div>
            <div class="score">
              <span class="star"><i class="fa-solid fa-medal"></i></span>
              <p class="mb-0">{{ score }}</p>
            </div>
          </div>
          <div class="screen">
            <div
              class="screen_bg"
              :style="{
                backgroundImage: `url(${require('../assets/img/quizgame/' +
                  currentGame.bg)})`,
              }"
            ></div>
            <div class="questions">
              <div v-if="idx < count">
                <h6>Question {{ idx + 1 }} / {{ count }}</h6>
                <h5 class="caption">
                  {{ arrayQuestion[idx]["caption"] }}
                </h5>
                <div>
                  <div class="skulls">
                    <i
                      v-for="(star, index) in getCeil(
                        arrayQuestion[idx]['difficulty']
                      )"
                      :key="index"
                      class="fa-solid fa-skull"
                    >
                    </i>
                  </div>
                </div>
                <div class="image">
                  <img
                    :src="
                      require(`../assets/img/quizgame/${arrayQuestion[idx]['img']}.jpg`)
                    "
                    :alt="arrayQuestion[idx]['img']"
                  />
                </div>
                <div class="quest">
                  <p class="text-2xl font-bold">
                    {{ arrayQuestion[idx]["question"] }}
                  </p>
                </div>
                <div class="answer">
                  <label
                    v-for="(answer, index) in arrayQuestion[idx]
                      .answers"
                    :key="index"
                    :for="index"
                  >
                    <input
                      :id="index"
                      type="radio"
                      class="input"
                      :value="index"
                      @change="answered($event, arrayQuestion)"
                      :disabled="selectedAnswer != ''"
                    />
                    {{ answer }}
                  </label>
                </div>
                <div class="m_btn">
                  <div
                    class="next"
                    @click="nextQuestion"
                    v-show="selectedAnswer != '' && idx < count - 1"
                  >
                    Next <i class="fa-solid fa-angle-right"></i>
                  </div>
                  <div
                    class="finish"
                    @click="showResults"
                    v-show="selectedAnswer != '' && idx == count - 1"
                  >
                    Results <i class="fa-solid fa-square-poll-vertical"></i>
                  </div>
                </div>
                <p class="message">{{ message }}</p>
              </div>
              <div v-else class="result">
                <h3 :class="setMedalColor()">{{ setMedalColor() }} Medal</h3>
                <div>
                  <p>
                    <i class="fa-regular fa-circle-check fa-2x correct"></i>
                    Correct Answers:
                    <span>{{ nCorrectAnswers }}</span>
                  </p>
                  <hr />
                  <p>
                    <i class="fa-regular fa-circle-xmark fa-2x wrong"></i>
                    Wrong Answers:
                    <span>{{ nWrongAnswers }}</span>
                  </p>
                  <hr />
                  <p>
                    <i
                      class="fa-solid fa-medal fa-2x"
                      :class="setMedalColor()"
                    ></i>
                    Score:
                    <span>{{ score }}</span>
                  </p>
                  <hr />
                </div>
                <div class="m_btn" @click="resetQuiz">
                  <i class="fa-solid fa-arrows-rotate fa-2x reload"></i>
                </div>
              </div>
            </div>
          </div>
          <section v-if="info" class="instructions">
            <h1>QUIZ GAME CHALLENGE</h1>
            <hr />
            <h4>{{ currentGame.info_title }}</h4>
            <div>
              <span @click="infos"
                ><i class="fa-solid fa-circle-left fa-2x"></i
              ></span>
            </div>
            <p>{{ currentGame.info_rules_1 }}</p>
            <p>{{ currentGame.info_rules_2 }}</p>
            <p>{{ currentGame.info_rules_3 }}</p>
            <p>{{ currentGame.score }}</p>
            <div class="calculate_score">
              <img src="../assets/img/quizgame/timerex.png" alt="timer" /><span
                >{{ currentGame.example_timer }}</span
              >
              <br />
              <img src="../assets/img/quizgame/skullex.png" alt="skulls" /><span
                >{{ currentGame.example_skull }}</span
              >
              <br />
              <span>(30 x 10) x 2 = 600</span>
            </div>
            <br />
            <p>{{ currentGame.info_rules_4 }}</p>
            <p>{{ currentGame.medal }}</p>
            <ul>
              <li>{{ currentGame.medal_wood }}</li>
              <li>{{ currentGame.medal_bronze }}</li>
              <li>{{ currentGame.medal_silver }}</li>
              <li>{{ currentGame.medal_gold }}</li>
            </ul>
            <p class="b_chance">{{ currentGame.play }}</p>
          </section>
        </section>
      </transition>
    </div>
  </div>
</template>
<script>
//MOVIES
import movies_eng from "../games/quiz/movies_eng.json";
import movies_ita from "../games/quiz/movies_ita.json";
import movies_fra from "../games/quiz/movies_fra.json";
//MUSICS
import musics_eng from "../games/quiz/musics_eng.json";
import musics_ita from "../games/quiz/musics_ita.json";
import musics_fra from "../games/quiz/musics_fra.json";
//SPORTS
import sports_eng from "../games/quiz/sports_eng.json";
import sports_ita from "../games/quiz/sports_ita.json";
import sports_fra from "../games/quiz/sports_fra.json";
//CULTURES
import cultures_eng from "../games/quiz/cultures_eng.json";
import cultures_ita from "../games/quiz/cultures_ita.json";
import cultures_fra from "../games/quiz/cultures_fra.json";

export default {
  name: "QuizView",
  components: {},
  data() {
    return {
      // MOVIES
      movies_eng,
      movies_ita,
      movies_fra,
      // MUSICS
      musics_eng,
      musics_ita,
      musics_fra,
      // SPORTS
      sports_eng,
      sports_ita,
      sports_fra,
      // CULTURES
      cultures_eng,
      cultures_ita,
      cultures_fra,
      // SETTINGS
      currentGame: [],
      info: false,
      secondsLeft: 0,
      welcome: true, //true
      gameplay: false, //false
      game: false,
      lang: "",
      // QUESTIONS
      questionNumber: 1,
      arrayQuestion: [],
      idx: 0,
      selectedAnswer: "",
      correctAnswers: "",
      nWrongAnswers: 0,
      nCorrectAnswers: 0,
      count: 10,
      bonus: 0,
      malus: false,
      partialScore: 0,
      score: 0,
      message: "",
      message_1: "",
      message_2: "",
      message_3: "",
    };
  },
  methods: {
    showGamePlay(param) {
      this.welcome = false;
      this.gameplay = true;
      this.lang = param;
    },
    play(param) {
      //this.resetQuiz();
      this.gameplay = false;
      this.game = true;
      this[param] = !this[param];
      param = param + "_" + this.lang;
      // MOVIES
      switch (param) {
        case "movie_eng":
          this.currentGame = JSON.parse(JSON.stringify(this.movies_eng));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, c'mon...";
          this.message_2 = "Hurry up!";
          this.message_3 = "Time ran out! No bonus in this round.";
          break;
        case "movie_ita":
          this.currentGame = JSON.parse(JSON.stringify(this.movies_ita));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          console.log(this.arrayQuestion);
          this.message_1 = "Oh, andiamo...";
          this.message_2 = "Il tempo scorre!";
          this.message_3 = "Tempo scaduto! Nessun bonus in questo round.";
          break;
        case "movie_fra":
          this.currentGame = JSON.parse(JSON.stringify(this.movies_fra));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, allez...";
          this.message_2 = "Dépêche-toi !";
          this.message_3 = "Le temps est écoulé ! Pas de bonus dans ce tour.";
          break;
        case "music_eng":
          this.currentGame = JSON.parse(JSON.stringify(this.musics_eng));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, c'mon...";
          this.message_2 = "Hurry up!";
          this.message_3 = "Time ran out! No bonus in this round.";
          break;
        case "music_ita":
          this.currentGame = JSON.parse(JSON.stringify(this.musics_ita));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, andiamo...";
          this.message_2 = "Il tempo scorre!";
          this.message_3 = "Tempo scaduto! Nessun bonus in questo round.";
          break;
        case "music_fra":
          this.currentGame = JSON.parse(JSON.stringify(this.musics_fra));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, allez...";
          this.message_2 = "Dépêche-toi !";
          this.message_3 = "Le temps est écoulé ! Pas de bonus dans ce tour.";
          break;
        case "sport_eng":
          this.currentGame = JSON.parse(JSON.stringify(this.sports_eng));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, c'mon...";
          this.message_2 = "Hurry up!";
          this.message_3 = "Time ran out! No bonus in this round.";
          break;
        case "sport_ita":
          this.currentGame = JSON.parse(JSON.stringify(this.sports_ita));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, andiamo...";
          this.message_2 = "Il tempo scorre!";
          this.message_3 = "Tempo scaduto! Nessun bonus in questo round.";
          break;
        case "sport_fra":
          this.currentGame = JSON.parse(JSON.stringify(this.sports_fra));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, allez...";
          this.message_2 = "Dépêche-toi !";
          this.message_3 = "Le temps est écoulé ! Pas de bonus dans ce tour.";
          break;
        case "general_eng":
          this.currentGame = JSON.parse(JSON.stringify(this.cultures_eng));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, c'mon...";
          this.message_2 = "Hurry up!";
          this.message_3 = "Time ran out! No bonus in this round.";
          break;
        case "general_ita":
          this.currentGame = JSON.parse(JSON.stringify(this.cultures_ita));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, andiamo...";
          this.message_2 = "Il tempo scorre!";
          this.message_3 = "Tempo scaduto! Nessun bonus in questo round.";
          break;
        case "general_fra":
          this.currentGame = JSON.parse(JSON.stringify(this.cultures_fra));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, allez...";
          this.message_2 = "Dépêche-toi !";
          this.message_3 = "Le temps est écoulé ! Pas de bonus dans ce tour.";
          break;
        default:
          this.currentGame = JSON.parse(JSON.stringify(this.cultures_eng));
          this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
          this.message_1 = "Oh, c'mon...";
          this.message_2 = "Hurry up!";
          this.message_3 = "Time ran out! No bonus in this round.";
      }
      this.countdown(45);
    },
    getMeRandomElements(sourceArray, neededElements) {
      var result = [];
      var shuffledResults = [];
      while (result.length < neededElements) {
        var item = sourceArray[Math.floor(Math.random() * sourceArray.length)];
        if (!result.includes(item)) {
          result.push(item);
        }
      }
      shuffledResults = result.sort(() => 0.5 - Math.random());
      return shuffledResults;
    },
    back() {
      this.resetQuiz();
      this.stopTimer();
      this.game = false;
      this.gameplay = true;
      this.currentGame = [];
      this.arrayQuestion = [];
    },
    infos() {
      this.info = !this.info;
      if (this.info) {
        this.stopTimer();
      } else {
        this.countdown(this.secondsLeft);
      }
    },
    getCeil(num) {
      let n = Math.ceil(num);
      return Math.ceil(n);
    },
    answered(e, db) {
      this.message = "";
      this.bonus = 0;
      this.partialScore = db[this.idx].difficulty * 50;
      this.malus = false;
      this.selectedAnswer = e.target.value;
      if (this.selectedAnswer == db[this.idx].correctAnswer) {
        this.nCorrectAnswers++;
        e.target.parentElement.style.background = "#00e900";
      } else {
        this.nWrongAnswers++;
        this.malus = true;
        e.target.parentElement.style.background = "#ff4a4a";
      }
      setTimeout(() => {
        document
          .querySelectorAll("input")
          .forEach((el) =>
            el.value == db[this.idx].correctAnswer
              ? (this.correctAnswers = el.parentElement.style.background =
                "#00e900")
              : (el.parentElement.style.opacity = 0.3)
          );
      }, 50);
      // CALCULATE SCORE
      if (!this.malus) {
        if (this.secondsLeft > 0) {
          this.bonus = this.secondsLeft * 10;
          this.bonus *= db[this.idx].difficulty;
          this.score += this.bonus + this.partialScore;
        }
        else {
          this.score += this.partialScore;
        }
      }
    },
    nextQuestion() {
      this.secondsLeft = 45;
      //this.countdown();
      this.message = "";
      this.idx++;
      this.selectedAnswer = "";
      document.querySelectorAll("input").forEach((el) => (el.checked = false));
      document
        .querySelectorAll("input")
        .forEach((el) => (el.parentElement.style.background = "transparent"));
      document
        .querySelectorAll("input")
        .forEach((el) => (el.parentElement.style.opacity = 1));
    },
    countdown(n) {
      this.secondsLeft = n;
      this.message = "";
      this.timer = setInterval(() => {
        if (!this.selectedAnswer) {
          if (this.secondsLeft > 0) this.secondsLeft--;
          if (this.secondsLeft.toString().length < 2)
            this.secondsLeft = `0${this.secondsLeft}`;
        }
        if (this.secondsLeft <= 0 && !this.selectedAnswer) {
          this.message = this.message_3;
        }
        if (
          this.secondsLeft > 0 &&
          this.secondsLeft <= 10 &&
          !this.selectedAnswer
        ) {
          this.message = this.message_2;
        }
        if (
          this.secondsLeft > 10 &&
          this.secondsLeft <= 25 &&
          !this.selectedAnswer
        ) {
          this.message = this.message_1;
        }
      }, 1000);
    },
    stopTimer() {
      clearInterval(this.timer);
    },
    showResults() {
      this.idx++;
    },
    setMedalColor() {
      if (this.score <= 6000) {
        return "wood";
      }
      if (this.score > 6000 && this.score <= 10000) {
        return "bronze";
      }
      if (this.score > 10000 && this.score <= 14000) {
        return "silver";
      } else {
        return "gold";
      }
    },
    resetQuiz() {
      this.idx = 0;
      this.selectedAnswer = "";
      this.nCorrectAnswers = 0;
      this.nWrongAnswers = 0;
      this.message = "";
      this.score = 0;
      this.bonus = 0;
      this.malus = false;
      this.secondsLeft = 45;
      this.arrayQuestion = [];
      this.arrayQuestion = this.getMeRandomElements(this.currentGame.questions, this.count);
    },
  },
};
</script>
<style lang="scss" scoped>
#quizgame {
  min-height: 100vh;
  width: 100vw;
  //padding-top: 30px;
  background: #000;
  color: #fefefe;

  @media all and (min-width: 992px) {
    padding-top: 80px;
  }

  .welcome {
    padding-top: 30px;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    h1 {
      text-align: center;
    }

    img {
      height: 300px;
      //animation: turn 10s infinite linear;
    }

    .flags {
      display: flex;
      justify-content: space-between;

      .flag {
        width: 75px;
        height: 50px;
        margin: 5px;

        img {
          width: 100%;
          height: 100%;
        }
      }
    }

    @keyframes turn {
      from {
        transform: rotate(0deg);
      }

      to {
        transform: rotate(360deg);
      }
    }
  }

  .gameplay {
    text-align: center;
    padding: 15px 30px 30px 30px;

    .cards {
      //background: #ff000075;
      width: 100%;
      height: 85vh;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;

      @media all and (min-width: 992px) {
        justify-content: space-between;
      }

      .card {
        flex: 0 1 calc(100% - 1em);
        height: 100px;
        background: rgb(0, 0, 0);
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        transition: all 0.5s ease-in-out;

        &:hover {
          span {
            opacity: 0;
          }
        }

        &>div {
          width: 100%;
          height: 100%;
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
          opacity: 0.3;
          transition: all 0.5s ease-in-out;

          &:hover {
            opacity: 0.9;
          }
        }

        .card_1 {
          background-image: url(../assets/img/quizgame/movie.jpg);
        }

        .card_2 {
          background-image: url(../assets/img/quizgame/music.jpg);
        }

        .card_3 {
          background-image: url(../assets/img/quizgame/sport.jpg);
        }

        .card_4 {
          background-image: url(../assets/img/quizgame/general_k.jpg);
        }

        @media all and (min-width: 992px) {
          flex: 0 1 calc(50% - 1em);
          height: 200px;
        }

        span {
          position: absolute;
          font-size: 1.3em;
          text-transform: uppercase;
          letter-spacing: 0.3rem;
          transition: all 0.5s ease-in-out;
        }
      }
    }
  }

  .games {
    text-align: center;
    padding: 30px;

    span {
      cursor: pointer;

      &:hover {
        color: rgb(173, 248, 24);
      }
    }

    .show {
      height: 100vh;
    }

    .control {
      display: flex;
      justify-content: space-between;

      .title {
        text-transform: capitalize;
      }
    }

    .console {
      display: flex;
      justify-content: space-evenly;

      .timer {
        display: flex;
        justify-content: center;
        align-items: center;
        //margin: 2px;

        .clock {
          display: inline-block;
          background: url(../assets/img/quizgame/clock.svg) no-repeat center;
          height: 25px;
          margin-right: 5px;
          aspect-ratio: 1/1;
        }
      }

      .score {
        display: flex;
        align-items: center;

        .star {
          color: rgb(221, 23, 23);
          margin-right: 2px;

          i {
            font-size: 1.5em;
          }
        }
      }
    }

    .screen {
      position: relative;
      height: 70vh;
      //border: 1px solid #fefefe3a;
      border-radius: 9px;

      .screen_bg {
        height: 100%;
        //background-image: url(../assets/img/quizgame/movie.jpg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        opacity: 0.1;
        transition: all 0.5s ease-in-out;
      }

      .questions {
        display: flex;
        justify-content: center;
        position: absolute;
        background: #0000007a;
        border-radius: 9px;
        //width: 90%;
        height: 90%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        @media all and (min-width: 992px) {
          width: 50%;
        }

        h6 {
          font-size: .8rem;
          opacity: 0.5;
        }

        .caption {
          text-transform: uppercase;
        }

        .image {
          height: 150px;
          width: 300px;
          margin: 0 auto;
          
          @media all and (min-width: 500px) {
            height: 250px;
            width: 400px;
          }

          img {
            height: 100%;
            width: 80%;

            @media all and (min-width: 500px) {
              //height: 100%;
              width: 100%;
            }
          }
        }

        .quest {
          height: 50px;
        }

        .answer {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          align-items: center;

          label {
            flex: 0 1 calc(50% - 1em);
            margin: 5px;
            padding: 5px;
            border: 1px solid #fefefe;

            &:hover {
              cursor: pointer;
              background: #2249bea2;
            }

            &:active {
              background: #2249be;
            }

            .input {
              display: none;
            }
          }
        }

        .m_btn {
          margin: 5px auto;
          width: 200px;
          color: #fefefe;
          cursor: pointer;

          .next,
          .finish {
            padding: 3px;
            border: 1px solid #fefefe;
            border-radius: 9px;
          }

          .next {
